.pl {
  
  margin-top:80px;
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
 
}



.pl-title {
  font-size: 60px;
  font-weight: 600;
  margin-bottom: 20px;
  text-shadow: 2px 2px 2px grey;
  -webkit-text-stroke: 1px white;
  animation: mymove 4s infinite;
	}
  
	@keyframes mymove {
		50% {box-shadow: 7px 7px 7px rgba(97, 88, 88, 0.8);}
	  }	
  


.pl-desc {
  margin:auto;
  width:65%;

  margin-bottom:-30px;
 }

.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin:auto;
  /* gap:40px; */
  
 
}
@media screen and (max-width: 480px) {
.pl {
  
  margin-top:50px;
}
} 
@media screen and (max-width: 480px) {
  .pl {
    padding: 10px;
  }

  .pl-texts {
    width: 100%;
  }

  .pl-desc {
    display: none;
  }
}
